/*
  Type scale: 1.414
 */
* {
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

body {
  color: #3E3B3B;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  line-height: 1.45;
  margin: 0;
  padding: 0;
  -webkit-print-color-adjust: exact;
}

h1, h2, h3 {
  font-weight: inherit;
  margin: 1.414rem 0 0.5rem;
}

h1 {
  font-size: 3.998rem;
}

h2 {
  font-size: 2.827rem;
}

h3 {
  font-size: 1.999rem;
}

h4 {
  font-size: 1.414rem;
}

p {
  margin-bottom: 1.3em;
}

strong {
  font-weight: 400;
}

a {
  color: inherit;
  font-weight: 400;
  text-decoration: none;
  border-bottom: 1px solid #FBFF00;
}

a:hover {
  border-bottom-color: inherit;
}

.Resume {
  display: grid;
  grid-gap: 1rem;
  grid-template-areas:
        "header"
        "main"
        "sidebar";
  margin: 0 auto;
  max-width: 980px;
  padding: 35px 20px;
  width: 100%;
}

@media (min-width: 767px) {
  .Resume {
    grid-template-areas:
          "header header"
          "main sidebar";
    grid-template-columns: 3fr 1fr;
    padding: 50px 35px;
  }
}

@media print {
  .Resume {
    min-width: 100%;
    padding: 0;
  }
}

.Resume-header {
  grid-area: header;
}

.Resume-career {
  grid-area: main;
}

.Resume-extra {
  grid-area: sidebar;
}

@media (min-width: 500px) {
  .Resume-extra {
    display: grid;
    grid-auto-columns: 1fr;
    grid-template-areas: "one";
    grid-gap: 1rem;
    grid-template-areas: "one two three";
  }
}

@media (min-width: 768px) {
  .Resume-extra {
    display: block;
  }
}

.ResumeHeader {
  display: grid;
}

.ResumeHeader-title {
  margin-bottom: 1.3rem;
  -webkit-order: 1;
          order: 1;
  width: 100%;
}

.ResumeHeader-contact {
  margin: 1rem 0;
  -webkit-order: 2;
          order: 2;
  width: 100%;
}

.ContactList {
  list-style-type: none;
  margin: 0;
  padding-left: 0;
}

.ResumeHeader-summary {
  font-size: 1.414rem;
  -webkit-order: 3;
          order: 3;
  width: 100%;
}

.ResumeHeader-summary p {
  margin-bottom: inherit;
}

.Title-primary {
  background-color: #FBFF00;
  border-radius: 5px;
  clear: left;
  float: left;
  font-size: 1.999rem;
  margin-top: 0;
  padding: 3px 10px;
}

@media (min-width: 800px) {
  .Title-primary {
    font-size: 2.827rem;
  }
}

.Section {
  margin-bottom: 2rem;
}

.SectionHeading {
  font-size: 1.414rem;
}

.SectionHeading--primary {
  background-color: #FBFF00;
  border-radius: 5px;
  margin-bottom: 1.5rem;
  padding: 3px 10px;
}

.Job {
  margin-bottom: 2rem;
}
/* Horrible - attempts to get break-inside: avoid; failed miserably in Chrome  */
@media print {
  .Job:nth-child(4) {
    /* padding-bottom: 100px; */
  }
}

.Job-link {
  font-weight: 300;
}

.Job-meta {
  font-weight: 400;
  display: block;
  border-left: solid 5px #FBFF00;
  margin: 0;
  padding: 5px 10px;
}

.Job-meta a {
  font-weight: inherit;
}

.TagList {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin: 0 -2px;
}

.Tag {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  background-color: #FBFF00;
  border-radius: 3px;
  display: inline-block;
  margin-bottom: 4px;
  margin-right: 2px;
  margin-left: 2px;
  min-width: 50px;
  padding: 2px;
  text-align: center;
}

